import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import 'bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'

import * as  serviceAPI from "./services/APIservice";
import * as  serviceMain from "./services/mainservice";
import * as sent from "./services/LineNotify";


import axios from 'axios'
import dateFormat from "dateformat";
Vue.prototype.$serviceAPI = serviceAPI
Vue.prototype.$main = serviceMain
Vue.prototype.$dt = dateFormat
Vue.prototype.$api = axios
Vue.prototype.$sent = sent




import VueSweetalert2 from 'vue-sweetalert2';
import 'sweetalert2/dist/sweetalert2.min.css';
const options = {
  confirmButtonColor: '#3866af',
  cancelButtonColor: '#ff7674',
};
Vue.use(VueSweetalert2, options)

import VCalendar from 'v-calendar';
Vue.use(VCalendar)


Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
