var axios = require('axios');
var FormData = require('form-data');


module.exports.sentmessage = async (message) => {
    try {
        var data = new FormData();
        console.log(message);
        data.append('message', String(message));
        var config = {
            method: 'post',
            url: 'https://notify-api.line.me/api/notify',
            headers: {
                'Authorization': 'Bearer fgt2VWjxHqqKyrg2xsGcxSZhqhSk0D9gYNeXWIjkQnB',
                ...data.getHeaders()
            },
            data: data
        };
        let getres = await axios(config)
        console.log(JSON.stringify(getres.data));
    } catch (error) {
        console.log(error);
    }

}
