<template>
  <div class="row m-0 p-0 hmain w-100 bfont">
    <div
      class="m-0 p-0 card"
      :class="
        $store.getters['loginActions/GET_NAV'] === true
          ? 'col-6 col-md-3 col-lg-2'
          : 'mywidth'
      "
    >
      <div class="container row m-0 p-0">
        <div class="col-12 mx-0 mt-3 p-0 overflow">
          <div class="m-0 p-0" v-for="(e, i) in menu" :key="i">
            <div class="m-0-p-0" @click="sentmenu(e.text)">
              <Boxmenu :icon="e.icon" :text="e.text" />
            </div>
          </div>
        </div>
        <div class="col-12 d-flex align-items-end mx-0 mt-5 p-0">
          <div type="button" class="btn btn-danger w-100" @click="logout()">
            Log out
          </div>
        </div>
      </div>
    </div>
    <!-- -------------------------- -->
    <div class="col m-0 p-2 w-100">
      <div class="card p-3">
        <div class="row">
          <!-- -------------------------- -->
          <div class="col-12 col-md-7 col-lg-8 m-0 p-2">
            <div class="col-12 p-0">
              <div class="row">
                <!-- ---------------------- -->
                <div class="col-8">
                  <p class="mainfont">
                    {{ $store.getters["loginActions/GET_MENU"] }}
                  </p>
                </div>
                <!-- --------------------- -->
                <div class="col">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="20"
                    height="20"
                    fill="#008B8B"
                    class="bi bi-cart3"
                    viewBox="0 0 16 16"
                  >
                    <path
                      d="M0 1.5A.5.5 0 0 1 .5 1H2a.5.5 0 0 1 .485.379L2.89 3H14.5a.5.5 0 0 1 .49.598l-1 5a.5.5 0 0 1-.465.401l-9.397.472L4.415 11H13a.5.5 0 0 1 0 1H4a.5.5 0 0 1-.491-.408L2.01 3.607 1.61 2H.5a.5.5 0 0 1-.5-.5zM3.102 4l.84 4.479 9.144-.459L13.89 4H3.102zM5 12a2 2 0 1 0 0 4 2 2 0 0 0 0-4zm7 0a2 2 0 1 0 0 4 2 2 0 0 0 0-4zm-7 1a1 1 0 1 1 0 2 1 1 0 0 1 0-2zm7 0a1 1 0 1 1 0 2 1 1 0 0 1 0-2z"
                    />
                  </svg>
                  <span
                    class="badge badge-pill badge-success"
                    v-if="$store.getters['loginActions/GET_ORDER'].length !== 0"
                    >{{ $store.getters["loginActions/GET_ORDER"].length }}</span
                  >
                </div>
              </div>
            </div>
            <div class="m-0 p-0 overflow" v-if="callmenu.length !== 0">
              <div
                class="mx-0 mb-2 p-0 card"
                v-for="(e, i) in callmenu"
                :key="i"
              >
                <Boxshow :callmenu="e.name" />
                <!-- <div class="col-6 m-0 px-3 py-2">{{ e.name }}</div> -->
              </div>
            </div>
          </div>
          <!-- -------------------------- -->
          <div class="col-12 col-md col-lg py-0 m-0 p-2">
            <div class="col-12 p-0 m-0">
              <p class="mainfont">รายการที่สั่ง</p>
            </div>
            <div class="col-12 card m-0 p-2" v-if="callmenu.length !== 0">
              <p
                v-if="$store.getters['loginActions/GET_ORDER'].length === 0"
                class="p-3"
              >
                ยังไม่มีรายการ
              </p>

              <div
                class="m-0 p-0"
                v-if="$store.getters['loginActions/GET_ORDER'].length !== 0"
              >
                <!-- --------------------------- -->

                <div class="form-group form-check">
                  <input
                    class="form-check-input"
                    type="radio"
                    name="exampleRadios"
                    id="exampleRadios1"
                    value="ทานที่ร้าน"
                    v-model="typecustomer"
                  />
                  <label class="form-check-label" for="exampleRadios1">
                    ทานที่ร้าน
                  </label>
                </div>
                <!-- -------------------- -->
                <div class="form-group form-check">
                  <input
                    class="form-check-input"
                    type="radio"
                    name="exampleRadios"
                    id="exampleRadios1"
                    value="สั่งกลับบ้าน"
                    v-model="typecustomer"
                  />
                  <label class="form-check-label" for="exampleRadios1">
                    สั่งกลับบ้าน
                  </label>
                </div>
                <!-- -------------------- -->
                <div class="form-group">
                  <label for="formGroupExampleInput">ชื่อลูกค้า</label>
                  <input
                    type="text"
                    class="form-control"
                    id="formGroupExampleInput"
                    v-model="namecus"
                  />
                </div>
                <!-- -------------------- -->
                <div class="form-group">
                  <label for="formGroupExampleInput">จำนวนลูกค้า</label>
                  <input
                    type="number"
                    class="form-control"
                    id="formGroupExampleInput"
                    v-model="cus"
                  />
                </div>
                <!-- -------------------- -->
                <div class="form-group">
                  <label for="formGroupExampleInput">เวลา</label>
                  <input
                    type="time"
                    class="form-control"
                    id="formGroupExampleInput"
                    v-model="time"
                  />
                </div>
                <!-- --------------------------- -->

                <div
                  class="m-0 p-0"
                  v-for="(e, i) in $store.getters['loginActions/GET_ORDER']"
                  :key="i"
                >
                  <p>
                    {{ e.name }}({{ e.size }} {{ e.com }}) *{{ e.count }}
                    <span>
                      <button
                        type="button"
                        class="btn btn-danger py-0 px-1 m-0"
                        @click="remove(e)"
                      >
                        -
                      </button></span
                    >
                  </p>
                </div>

                <!-- {{ $store.getters["loginActions/GET_ORDER"] }} -->
                <button
                  type="button"
                  class="btn btn-info w-100"
                  @click="confirm()"
                >
                  ส่งออเดอร์
                </button>
              </div>
            </div>
          </div>
          <!-- ----------------------- -->
          <!-- {{ callmenu.length }} -->
          <div class="col-12 text-center mx-0 my-5" v-if="callmenu.length == 0">
            <div class="spinner-border text-info" role="status"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Boxmenu from "@/components/layout/Boxmenu.vue";
import Boxshow from "@/components/menu_order/Boxshow.vue";
export default {
  name: "HomeFame",
  components: { Boxmenu, Boxshow },
  data() {
    return {
      typecustomer: "ทานที่ร้าน",
      time: "",
      cus: "",
      namecus: "",
      menu: [
        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/roasted-chicken.png",
          text: "อาหารเเนะนำ",
        },
        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/roasted-chicken.png",
          text: "อาหารจานด่วน",
        },
        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/roasted-chicken.png",
          text: "เมนูปลา",
        },
        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/roasted-chicken.png",
          text: "เมนูอาหารอีสาน",
        },
        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/roasted-chicken.png",
          text: "เมนูยำ",
        },
        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/roasted-chicken.png",
          text: "เมนูต้ม",
        },
        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/roasted-chicken.png",
          text: "เมนูแกง",
        },
        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/roasted-chicken.png",
          text: "เมนูไข่",
        },

        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/roasted-chicken.png",
          text: "เมนูทอด",
        },
        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/roasted-chicken.png",
          text: "เมนูผัด",
        },

        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/roasted-chicken.png",
          text: "เมนูอาหารป่า",
        },

        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/roasted-chicken.png",
          text: "สเต็ก",
        },
        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/roasted-chicken.png",
          text: "สลัด",
        },

        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/roasted-chicken.png",
          text: "สปาเก็ตตี้",
        },
        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/roasted-chicken.png",
          text: "ของหวาน",
        },

        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/burger.png",
          text: "คาเฟ่-เครื่องดื่ม",
        },
        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/burger.png",
          text: "คาเฟ่-ไอศกรีม",
        },
        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/burger.png",
          text: "คาเฟ่-ขนมปัง",
        },
        {
          icon: "https://raw.githubusercontent.com/Wuttipong-B/order/gh-pages/img/burger.png",
          text: "คาเฟ่-น้ำแข็ง",
        },
      ],
      callmenu: [],
    };
  },
  mounted() {
    this.sentmenu(this.menu[0].text);
    this.getorder();
  },
  methods: {
    async confirm() {
      try {
        //console.log(this.$store.getters["loginActions/GET_ORDER"]);

        let message = `ออเดอร์วันที่ ${this.$dt(new Date(), "dd-mm-yyyy")}
${this.typecustomer}
ชื่อลูกค้า ${this.namecus}
จำนวน ${this.cus} คน
เวลา ${this.time} น.
        ---- รายการอาหาร ----
        `;
        var e = this.$store.getters["loginActions/GET_ORDER"];
        for (var i in e) {
          message = `${message} 
${e[i].name} (${e[i].size} ${e[i].com})  x${e[i].count}`;
          // console.log(`${e[i].name}(${e[i].size}) *${e[i].count}`);
        }

        var data = JSON.stringify({
          message: message,
        });
        let getToken = atob(atob(localStorage.getItem("user")));
        var config = {
          method: "post",
          url: "https://api-menu-bankangna.herokuapp.com/api/menus/line",
          headers: {
            Authorization: "Bearer " + getToken,
            "Content-Type": "application/json",
          },
          data: data,
        };

        let gettoken = await this.$api(config);
        this.$main.showSuccessAlert(this, "ส่งออเดอร์เรียบร้อย");
        this.redata();
      } catch (error) {
        this.$main.showErrorAlert(this, error);
      }
    },
    redata() {
      this.typecustomer = "";
      this.cus = "";
      this.time = "";
      this.namecus = "";
      this.$store.dispatch("loginActions/removeodrer");
      this.$store.dispatch("loginActions/getodrer");
    },
    remove(value) {
      //console.log(value);
      let data = { size: value.size, name: value.name, com: value.com };
      this.$store.dispatch("loginActions/removeorder", data);
    },
    getorder() {
      this.$store.dispatch("loginActions/getodrer");
    },
    async logout() {
      this.$store.dispatch("loginActions/logoutaction");
      this.$router.push({ name: "Login" });
    },
    async sentmenu(value) {
      // console.log(value);
      try {
        this.$store.dispatch("loginActions/menu", value);
        var data = JSON.stringify({
          type: value,
        });
        // let getToken = atob(atob(this.$store.getters["loginActions/GET_NAV"]));
        let getToken = atob(atob(localStorage.getItem("user")));
        var config = {
          method: "post",
          url: "https://api-menu-bankangna.herokuapp.com/api/menus/menu_type",
          headers: {
            Authorization: "Bearer " + getToken,
            "Content-Type": "application/json",
          },
          data: data,
        };
        this.callmenu = [];
        let gettoken = await this.$api(config);
        //console.log(gettoken.data.data);
        this.callmenu = gettoken.data.data;
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>

<style scoped>
.hmain {
  height: 95vh;
}
.mywidth {
  width: 90px;
}
.overflow {
  height: 84.5vh;
  overflow-x: hidden;
  overflow-y: auto;
}
.overflow ::-webkit-scrollbar {
  display: none;
}
</style>