// import AuthService from '../services/auth.service';
import * as main from '../../services/mainservice';
import * as constant from '../../services/constant';
import * as serviceAPI from "../../services/APIservice";
const user = main.parseJwtByLocalStorage("user");
let time
const initialState = user ?
  {
    auth: true,
    user: user.QTUSERNAME,
    nav: false,
    menu: null,
    order: []

  } :
  {
    auth: false,
    user: null,
    nav: false,
    menu: null,
    order: []
  };


export const loginActions = {
  namespaced: true,
  state: initialState,
  mutations: {
    MUT_USER(state, value) {
      state.user = value;
    },
    MUT_LOGIN(state, value) {
      state.auth = value;
    },
    MUT_LOGOUT(state, value) {
      state.auth = false;
    },
    MUT_NAV(state, value) {
      state.nav = value;
    },
    MUT_MENU(state, value) {
      state.menu = value;
    },
    MUT_ORDER(state, value) {
      state.order = value;
    },

  },
  getters: {
    GET_USER(state) {
      return state.user;
    },
    GET_AUTH(state) {
      return state.auth;
    },
    GET_NAV(state) {
      return state.nav;
    },
    GET_MENU(state) {
      return state.menu;
    },
    GET_ORDER(state) {
      return state.order;
    },


  },

  actions: {

    loginaction({ commit }, para_token) {
      let getEncodeAccess_token = btoa(btoa(para_token))
      localStorage.setItem(constant.TOKEN_KEY, getEncodeAccess_token);
      let getUser = main.parseJwtByLocalStorage(constant.TOKEN_KEY)

      commit('MUT_LOGIN', true)
      commit('MUT_USER', getUser.QTUSERNAME)


    },
    logoutaction({ commit }) {
      // localStorage.removeItem(constant.TOKEN_KEY)

      localStorage.clear();
      commit('MUT_LOGOUT')
      commit('MUT_USER', null)
      // location.reload();
    },
    checkauth({ commit }) {
      let user = localStorage.getItem("user");
      if (user == null) commit('MUT_LOGIN', false)
    },

    nav({ commit }) {
      let mynav = this.getters['loginActions/GET_NAV']
      commit('MUT_NAV', !mynav)

    },
    menu({ commit }, value) {
      //console.log(value);
      commit('MUT_MENU', value)
    },
    getodrer({ commit }) {
      var order = JSON.parse(localStorage.getItem('order'));

      if (order == null) {
        commit('MUT_ORDER', [])
      }
      else { commit('MUT_ORDER', order) }

    },
    removeodrer({ commit }) {
      localStorage.removeItem('order')

    },
    addorder({ commit }, value) {
      //console.log(value);
      let order = this.getters['loginActions/GET_ORDER']

      if (order.length == 0) {
        value.count = 1
        order.push(value)

        localStorage.setItem("order", JSON.stringify(order))
        this.dispatch('loginActions/getodrer')
      }
      else {
        const index = order.findIndex((e) => e.name === value.name && e.size === value.size && e.com === value.com);

        if (index === -1) {
          // ไม่เจอ
          value.count = 1
          order.push(value)
          localStorage.setItem("order", JSON.stringify(order))
          this.dispatch('loginActions/getodrer')
        } else {
          // เจอ

          order[index].count = order[index].count + 1
          localStorage.setItem("order", JSON.stringify(order))
          this.dispatch('loginActions/getodrer')
        }
      }
    }
    ,
    removeorder({ commit }, value) {
      //console.log(value); 
      let order = this.getters['loginActions/GET_ORDER']

      if (order.length == 0) {

      }
      else {
        const index = order.findIndex((e) => e.name === value.name && e.size === value.size && e.com === value.com);
        //console.log(index)
        if (index !== -1) {
          // เจอ
          // value.count = 1
          if (order[index].count > 0) {
            order[index].count = order[index].count - 1
            if (order[index].count === 0) {
              order.splice(index, 1)
            }
            localStorage.setItem("order", JSON.stringify(order))
            this.dispatch('loginActions/getodrer')
          }


        }
      }
    }
  },

};